@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* Form labels */
label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
}

/* Form inputs */
input[type="text"],
input[type="email"],
input[type="password"],
select {
  width: 100%;
  padding: 8px;
  margin-bottom: 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

/* Buttons */
button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* Table Styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px auto;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

/* Responsive Design */
@media (max-width: 600px) {
  form {
    padding: 15px;
  }

  input[type="text"],
  input[type="email"],
  input[type="password"],
  select {
    font-size: 14px;
  }

  button {
    font-size: 14px;
    padding: 8px 12px;
  }

  table {
    font-size: 14px;
  }
}
